<template>
  <el-dialog :title="roleInfo.roleId?'修改角色':'新增角色'"  :visible.sync="show" center width="500px" @close="resetForm('ruleForm')" :close-on-click-modal="false">
    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" class="ruleForm" label-width="130px">
      <el-form-item label="角色名称：" prop="roleName">  
         <el-input v-model.trim="ruleForm.roleName" placeholder="请输入角色名称" class="input-box" clearable maxlength="20"></el-input>
      </el-form-item>
      <el-form-item label="角色描述：" prop="remark">  
        <el-input v-model.trim="ruleForm.remark" type="textarea" placeholder="请输入角色描述" class="input-box" clearable maxlength="100"></el-input>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="resetForm('ruleForm')">取 消</el-button>
      <el-button type="primary" @click="submitForm('ruleForm')">确 认</el-button>
    </div>
 </el-dialog>
</template>

<script>
export default {
  name: 'addRole',
  props: {
    value: {
      type: Boolean
    },
    roleInfo:{
      type:Object,
      default: ()=>{}
    }
  },
  model: {
    prop: 'value',
    event: 'change'
  },
  watch: {
    value (n) {
      this.show = this.value
      if(this.show === true){
        if(this.roleInfo.roleId){
          for(let key in this.ruleForm){
            this.ruleForm[key] = this.roleInfo[key]
          }
        }else{
          this.initForm() //初始化表单
        }
      }
    }
  },
  data () {
    return {
       show:this.value,
       ruleForm:{
         roleName:'',//角色名称
         remark:'', //角色描述
         projectType:2 //所属项目（1 公卫 2 家医平台）
       },
       rules: {
        roleName: [
          { required: true, message: '角色名称不能为空', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    //保存设置
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          let params = {}
          params.projectType =2
          params = Object.assign({},this.ruleForm,params)
          if(this.roleInfo.roleId){
            params.roleId = this.roleInfo.roleId
            this.editRole(params)
          }else{
            this.addRole(params)
          }
        }
      });
    },
    //添加角色
    addRole(params){
      this.$http.post(process.env.VUE_APP_API+'/role/addRole',params,false).then(res => {
        if(res.code===200){
          this.$message.success('添加成功');
          this.show = false
          this.$emit('change', false)
          this.$emit('refresh') //刷新列表
        }else{
          this.$message.error(res.msg);
        }
      })
      .catch(error => {
        this.$message.error(error);
      });
    },
    //编辑角色
    editRole(params){
      this.$http.post(process.env.VUE_APP_API+'/role/updateRole',params,false).then(res => {
        if(res.code===200){
          this.$message.success('修改成功');
          this.show = false
          this.$emit('change', false)
          this.$emit('refresh') //刷新列表
        }else{
          this.$message.error(res.msg);
        }
      })
      .catch(error => {
        this.$message.error(error);
      });
    },
    //初始化表单
    initForm(){
      Object.keys(this.ruleForm).forEach(k => this.ruleForm[k] = '')
    },
    //取消设置
    resetForm(formName) {
      this.show = false
      this.$emit('change', false)
      this.$nextTick(()=>{
        this.$refs[formName].clearValidate();
      })
    }
  }
}
</script>

<style lang='less' scoped>
  /* 修改状态弹窗 */
  .ruleForm{
    .input-box{
      width:300px;
    }
  }
</style>