<!-- 权限管理 -->
<template>
  <div class="list-common-box">
    <!-- 标题 -->
    <div class="title-box">
       <h2>权限管理</h2>
       <div class="rightbox">
        <div class="item" @click="handleAdd()">
           <cc-svg-icon icon-class="add"></cc-svg-icon>
           <span>新增</span>
        </div>
      </div>
    </div>
    <!-- 主要数据 -->
    <div class="card-box">
      <!-- 搜索 -->
      <div class="search-bar">
        <!-- 左 -->
        <div class="search-left">
          <div class="search-item">
            <span class="search-label">角色名称</span>
            <el-input v-model.trim="searchKey.roleName" placeholder="请输入角色名称" clearable></el-input>
          </div>
        </div>
        <div class="search-right">
          <el-button type="primary" plain icon="el-icon-search" @click="querylist()">查询</el-button>
          <el-button type="primary" plain icon="el-icon-delete" @click="Refreshlist()">清空</el-button>
        </div>
      </div>
      <!-- 表格 -->
      <div class="table-box">
        <el-table :data="dataList" tooltip-effect="dark" stripe height="100%"> 
          <el-table-column label="序号" align="center" width="80">
            <template slot-scope="scope">
              {{scope.$index+(pageNum - 1) * pageSize + 1}}
            </template>
          </el-table-column>
          <el-table-column prop="roleName" label="角色名称" align="center">
            <template slot-scope="scope">
              {{scope.row.roleName?scope.row.roleName:EMPTYTEXT}}
            </template>
          </el-table-column>
          <el-table-column prop="remark" label="角色描述" align="center" width="190" :show-overflow-tooltip='true'>
            <template slot-scope="scope">
              {{scope.row.remark?scope.row.remark:EMPTYTEXT}}
            </template>
          </el-table-column>
          <el-table-column prop="labelShortName" label="关联权限" align="center">
            <template slot-scope="scope">
              <el-button type="primary" @click="handleRelation(scope.row)" class="green">关联权限</el-button>
            </template>
          </el-table-column>
          <el-table-column prop="createTimeStr" label="创建时间"  align="center" width="170">
            <template slot-scope="scope">
              {{scope.row.createTimeStr?scope.row.createTimeStr:EMPTYTEXT}}
            </template>
          </el-table-column>
          <el-table-column label="操作"  align="center" width="120">
            <template slot-scope="scope">
              <cc-svg-icon icon-class="edit" @click="handleAdd(scope.row)"></cc-svg-icon>
              <cc-svg-icon icon-class="delete" @click="handleDelete(scope.row)"></cc-svg-icon>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <!-- 分页 -->
      <div class="page-box">
         <Paginaction class='pagination' :goPage="pageNum" :totalNum="totalNum" @updateList="changePage" ref="paginationEvent"/>
      </div>
    </div>
    <!-- 新增.编辑角色弹窗 -->
    <addRole v-model="roleDialog" :roleInfo="roleInfo" @refresh="getDatalist"></addRole>
  </div>
</template>

<script>
  import addRole from './addRole' //添加编辑角色
  export default {
    name: 'authority-list',
    components: {
      addRole
    },
    data() {
      return {
        searchKey:{
          roleName:'',//角色名称
        },
        pageNum:1,//当前页
        pageSize:10,//每页条数
        totalNum: 0, //总条数
        dataList: [],//角色列表
        roleDialog:false,//添加、修改角色弹窗
        roleInfo:{},//角色信息
      }
    },
    created() {
       this.getDatalist() //获取角色列表
    },
    methods: {
      //新增编辑角色
      handleAdd(scope){
        this.roleDialog = true
        this.roleInfo = scope?scope:{}
      },
      //关联权限
      handleRelation(scope){
        this.$router.push({
          path: '/authority-manage/relatedPermissions',
          query:{
            id:scope.roleId,
            name:scope.roleName
          }
        })
      },
      //查询
      querylist() {
        this.pageNum = 1
        this.getDatalist()
      },
      //重置
      Refreshlist() {
        this.pageNum = 1
        this.pageSize = 10
        Object.keys(this.searchKey).forEach(k => this.searchKey[k] = '')
        this.getDatalist()
      },
      //分页改变
      changePage(query){
        this.pageNum = query.pageNum
        this.pageSize = query.pageSize
        this.getDatalist()
      },
      //获取角色列表
      getDatalist() {
        this.$emit("viewLoad", true);
        let params = {}
        params.pageNum  = this.pageNum
        params.pageSize = this.pageSize
        params.projectType = 2
        params = Object.assign({},params,this.searchKey)
        this.$http.post(process.env.VUE_APP_API + '/role/getRoleInfoList', params).then(res => {
          if (res.code===200) {
            this.dataList = res.data.list || [] //角色列表
            this.totalNum = res.data.total //总条数
            this.pageNum = res.data.pageNum //当前页
            this.pageSize = res.data.pageSize //每页条数
          } else {
            this.$message.error(res.msg);
          }
          this.$emit("viewLoad", false);
        })
        .catch(error => {
          this.$emit("viewLoad", false);
          this.$message.error(error);
        });
      },
      //删除角色
      handleDelete (scope) {
        this.$confirm('确定删除"'+scope.roleName+'"吗？', '提示', {
          confirmButtonText: '确 认',
          cancelButtonText: '取 消',
          customClass:'infoMessage',
          center:true
        }).then(() => {
          this.$http.post(process.env.VUE_APP_API+'/role/deleteRole',{
            roleIds:scope.roleId.split(",")
          },false).then(res => {
            if(res.code===200){
              this.$message.success('删除成功');
              this.getDatalist() //刷新列表
            }else{
              this.$message.error(res.msg);
            }
          })
          .catch(error => {
            this.$message.error(error);
          });
        })
      }
    }
  }
</script>
<style lang='less' scoped>
</style>